.no-link {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  font-size: 0;
}

.pink-btn {
  border: 2px solid #fa198c;
  position: relative;
  display: inline-block;
  padding: 15px;
  font-weight: 700;
  letter-spacing: 0.008em;
  transition: all 0.3s ease-in-out;
  color: #fff;
  z-index: 3;
}

.pink-btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background: #fa198c;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: all 0.3s ease-in-out;
}

.pink-btn:hover {
  color: #fa198c;
}

.pink-btn:hover::after {
  width: 0;
  right: 0;
  left: auto;
}

.text-pink {
  color: #fa198c;
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
